import { NextRequest, NextResponse } from "next/server";
import { prisma } from "@/lib/prisma";
import { auth } from "@/lib/auth";
import { turnOnLight } from "@/lib/esp";

// GET /api/sessions - получить историю сессий
export async function GET(request: NextRequest) {
  try {
    const session = await auth();
    if (!session) {
      return NextResponse.json({ error: "Unauthorized" }, { status: 401 });
    }

    const searchParams = request.nextUrl.searchParams;
    const tableId = searchParams.get("tableId");
    const status = searchParams.get("status");
    const limit = parseInt(searchParams.get("limit") || "50");
    const offset = parseInt(searchParams.get("offset") || "0");

    const where: {
      tableId?: number;
      status?: "ACTIVE" | "COMPLETED" | "EXPIRED";
    } = {};

    if (tableId) {
      where.tableId = parseInt(tableId);
    }
    if (status) {
      where.status = status as "ACTIVE" | "COMPLETED" | "EXPIRED";
    }

    const [sessions, total] = await Promise.all([
      prisma.session.findMany({
        where,
        include: {
          table: {
            select: { id: true, name: true, hourlyRate: true },
          },
        },
        orderBy: { startedAt: "desc" },
        take: limit,
        skip: offset,
      }),
      prisma.session.count({ where }),
    ]);

    return NextResponse.json({ sessions, total, limit, offset });
  } catch (error) {
    console.error("Error fetching sessions:", error);
    return NextResponse.json(
      { error: "Internal server error" },
      { status: 500 }
    );
  }
}

// POST /api/sessions - начать новую сессию
export async function POST(request: NextRequest) {
  try {
    const authSession = await auth();
    if (!authSession) {
      return NextResponse.json({ error: "Unauthorized" }, { status: 401 });
    }

    const body = await request.json();
    const { tableId, duration } = body;

    if (!tableId) {
      return NextResponse.json(
        { error: "tableId is required" },
        { status: 400 }
      );
    }

    // Проверяем существование стола
    const table = await prisma.billiardTable.findUnique({
      where: { id: tableId },
    });

    if (!table) {
      return NextResponse.json({ error: "Table not found" }, { status: 404 });
    }

    // Проверяем нет ли активной сессии
    const activeSession = await prisma.session.findFirst({
      where: {
        tableId,
        status: "ACTIVE",
      },
    });

    if (activeSession) {
      return NextResponse.json(
        { error: "Table already has an active session" },
        { status: 400 }
      );
    }

    // Создаём новую сессию с ценой стола на момент начала
    const session = await prisma.session.create({
      data: {
        tableId,
        duration: duration || null, // null = свободное время
        hourlyRate: table.hourlyRate, // Сохраняем текущую цену стола
        status: "ACTIVE",
      },
      include: {
        table: {
          select: { id: true, name: true, hourlyRate: true },
        },
      },
    });

    // Отправляем команду на ESP для включения света
    const espResult = await turnOnLight(table.espIpAddress, table.gpioPin);
    if (!espResult.success) {
      console.error(`Failed to turn on light for table ${tableId}:`, espResult.error);
    }

    return NextResponse.json(session, { status: 201 });
  } catch (error) {
    console.error("Error creating session:", error);
    return NextResponse.json(
      { error: "Internal server error" },
      { status: 500 }
    );
  }
}
