// Функции для управления ESP-12F

const ESP_TIMEOUT = 5000; // 5 секунд таймаут

export interface ESPCommandResult {
  success: boolean;
  error?: string;
}

// Отправить команду на ESP для включения/выключения света
export async function sendESPCommand(
  espIpAddress: string,
  gpioPin: number,
  turnOn: boolean
): Promise<ESPCommandResult> {
  try {
    const action = turnOn ? "on" : "off";
    const url = `http://${espIpAddress}/control?pin=${gpioPin}&action=${action}`;

    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), ESP_TIMEOUT);

    const response = await fetch(url, {
      method: "GET",
      signal: controller.signal,
    });

    clearTimeout(timeoutId);

    if (response.ok) {
      return { success: true };
    } else {
      return {
        success: false,
        error: `ESP returned status ${response.status}`
      };
    }
  } catch (error) {
    if (error instanceof Error) {
      if (error.name === "AbortError") {
        return { success: false, error: "Connection timeout" };
      }
      return { success: false, error: error.message };
    }
    return { success: false, error: "Unknown error" };
  }
}

// Включить свет на столе
export async function turnOnLight(
  espIpAddress: string,
  gpioPin: number
): Promise<ESPCommandResult> {
  console.log(`Turning ON light: ESP=${espIpAddress}, GPIO=${gpioPin}`);
  return sendESPCommand(espIpAddress, gpioPin, true);
}

// Выключить свет на столе
export async function turnOffLight(
  espIpAddress: string,
  gpioPin: number
): Promise<ESPCommandResult> {
  console.log(`Turning OFF light: ESP=${espIpAddress}, GPIO=${gpioPin}`);
  return sendESPCommand(espIpAddress, gpioPin, false);
}
